<template>
  <div class="tobepaid">
    <hx-Empty v-show="show"></hx-Empty>

    <div class="whole-item" v-for="(item,index) in lists" :key="index">
      <div class="whole-header">
        订单:
        <span>{{item.time}}</span>
        <div>{{item.timeDate | dataFormat}}</div>
        <div class="header-right">{{$store.state.order.typelist[item.type]}}</div>
      </div>
      <div class="item" v-for="(items,index) in item.goodlist" :key="index">
        <div class="item-img">
          <img :src="items.url" alt />
        </div>
        <div class="item-tit van-multi-ellipsis--l2">{{items.title}}</div>
        <div class="item-price">
          <div class="item-pro">
            ￥
            <span>{{items.price}}</span>
          </div>
          <div class="pro-tit van-ellipsis">{{items.specifications}}</div>
          <div class="count">
            x
            <span>{{items.num}}</span>
          </div>
        </div>
      </div>
      <div>
        <div class="whole-bottom" v-if="item.type == 1">
          <span class="price">￥{{item.price}}</span>
          <div class="whole-btn">
            <van-button type="primary" size="mini" @click="Payment(item,index)">支付订单</van-button>
            <van-button type="danger" size="mini" @click="Del(item,index)">删除订单</van-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import hxEmpty from "../../components/hxEmpty";

export default {
  data() {
    return {
      lists: [],
      show: false
    };
  },
  methods: {
    Payment(item, index) {
      this.$store.commit("order/Payment", item);
      this.lists.splice(index, 1);
    },
    Del(item, index) {
      this.$store.commit("order/Del", item);
      this.lists.splice(index, 1);
    }
  },
  created() {
    // console.log(this.$store.state.order.lists)
    let array = this.$store.state.order.lists;
    array.forEach(v => {
      if (v.type == 1) {
        this.lists.push(v);
      }
    });
    if (this.lists.length < 1) {
      this.show = true;
    }
  },
  updated() {
    if (this.lists.length < 1) {
      this.show = true;
    }
  },
  filters: {
    dataFormat(dataString) {
      var dd = new Date(dataString);
      // 获取年月日
      var y = dd.getFullYear();
      var m = dd.getMonth() + 1;
      var d = dd.getDate();
      // 获取时分秒
      var hh = dd.getHours();
      var mm = dd.getMinutes();
      var ss = dd.getSeconds();
      return `${y}-${m}-${d} ${hh}:${mm}:${ss}`;
    }
  },
  components: {
    hxEmpty
  }
};
</script>

<style lang="less" scoped>
.tobepaid {
  .whole-item {
    background-color: #ffffff;
    width: calc(100% - 20px);
    margin-left: 10px;
    margin-top: 10px;
    overflow: hidden;
    border-radius: 5px;
    .whole-header {
      height: 50px;
      padding-top: 10px;
      line-height: 20px;
      font-size: 13px;
      padding-left: 10px;
      position: relative;
      .header-right {
        position: absolute;
        top: 15px;
        right: 30px;
        color: #666666;
        font-size: 16px;
      }
    }
    .item {
      overflow: hidden;
      div {
        float: left;
      }
      .item-img {
        width: 80px;
        height: 80px;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .item-tit {
        height: 40px;
        margin-top: 10px;
        margin-left: 10px;
        width: calc(100% - 120px);
        font-size: 14px;
      }
      .item-price {
        margin-left: 10px;
        width: calc(100% - 120px);
        .item-pro {
          color: red;
        }
        .count {
          float: right;
          position: relative;
          top: 3px;
          font-size: 13px;
        }
        .pro-tit {
          font-size: 12px;
          width: 100px;
          margin-left: 20px;
          margin-top: 3px;
        }
      }
    }
    .whole-bottom {
      height: 30px;
      margin-top: 10px;
      margin-left: 10px;
      width: 100%;
      .price {
        float: left;
        color: red;
      }
      .whole-btn {
        float: right;
        width: 130px;
        button {
          padding-left: 3px;
          padding-top: 1px;
        }
      }
    }
  }
}
</style>